<template>
	<div class="toplist">
		<div class="top">
			<img :src="data.img" alt="">
			<div class="font">
				<h1 class='title'>{{data.topname}}</h1>
				<p>{{data.topname}} 第130天</p>
				<p>2019-5-16更新</p>
			</div>
			<p class='btn'>播放全部</p>
		</div>
		<p class='topfont'>排行榜 共100首</p>
		<ul class="list" v-if='data.data'>
			<li v-for='item,index in data.data.data.tracks'>
				<p class='song'><span :class='icon'+index></span><span v-if='index<3'>{{index+1}}</span>&nbsp;&nbsp;&nbsp;{{item.name}}</p>
				<p class='sing'><i></i>&nbsp;&nbsp;&nbsp;<span v-for='item1,index1 in item.singer'>{{item1.name}}&nbsp;&nbsp;</span></p>
			</li>
		</ul>
	</div>
</template>
<script>
	import axios from 'axios';
	export default{
		data(){
			return{
				data: {}
			}
		},
		mounted(){
			this.data = this.$route.params;
			console.log(this.data)
		}
	}
</script>
<style scoped>
	.top{
		display:flex;
		height: 250px;
		background: rgba(0,0,0,.8);
		position: relative;
	}
	.top img{
		width: 150px;
		height: 150px;
		margin: 15px 0 0 15px;
	}
	.top .font{
		margin-top: 50px;
		margin-left: 15px;
	}
	.top .font .title{
    	font-size: 18px;
    	color: #fff;
	}
	.top .font p{
		color: #fff;
		font-size: 13px;
		margin-top: 10px;
	}
	.top .btn{
		position: absolute;
		bottom: 20px;
		left: 50%;
		margin-left: -85px;width: 170px;
		height: 40px;
		text-align: center;
		line-height: 40px;
		color: #fff;
		background: #31c27c;
		border-radius: 20px;
		
	}
	.topfont{
		font-size: 14px;
		color: #777;
		padding: 20px 0 0 20px;
	}
	.list{
		margin-top: 20px;
	}
	.list li{
		padding-left: 10px;
		margin-top: 20px;
	}
	.list li p i{
		display: inline-block;
	    width: 7px;
	    height: 8px;
	    line-height: 999px;
	    overflow: hidden;
	    background-image: url('/static/images/1.png');
	    background-repeat: no-repeat;
	    background-size: 16px;
	}
	.list li .sing{
		color: #777;
		font-size: 13px;
		margin-top: 3px;
	}
	.list li .song{
		color: #333;
	}
	.list li .song span{
		display: inline-block;
		width: 7px;
	}
	.list li .song .on{
		color: #FF400B;
	}
</style>


